.modal-window__locations, .modal-window__franchise {
  height: 300px;
  width: 100%;
  background-color: var(--txt-color-btnwhi);
  padding: 3.5% 4%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-window__franchise {
  height: 420px;
}

.is-hiddden {
  opacity: .2;
  visibility: hidden;
  pointer-events: none;
  transform: translate(0%, 100%);
}

.modal-window__close-btn {
  fill: var(--txt-color-blck);
  width: 20px;
  height: 20px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(-14px, 14px);
}

.modal-window__close-icon {
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 480px) {
  .modal-window__locations, .modal-window__franchise {
    height: 354px;
    width: 440px;
    padding: 14px 40px;
  }

  .modal-window__franchise {
    height: 517px;
  }

  .modal-window__close-btn {
    width: 20px;
    height: 20px;
    transform: translate(-20px, 20px);
  }
}

@media screen and (min-width: 768px) {
  .modal-window__locations, .modal-window__franchise {
    height: 458px;
    width: 568px;
    padding: 32px 60px;
  }

  .modal-window__franchise {
    height: 668px;
  }

  .modal-window__close-btn {
    width: 20px;
    height: 20px;
    transform: translate(-20px, 20px);
  }
}

